async function findLastRow(ws, context) {
  // Carga el rango utilizado
  const usedRange = ws.getUsedRange(true);
  usedRange.load('rowCount');
  await context.sync(); // Sincroniza el contexto para asegurarse de que las propiedades se carguen

  // Define el rango para buscar desde AA10 hacia abajo hasta el final del rango utilizado
  const range = ws.getRange("AA10:AA" + usedRange.rowCount);

  // Carga los valores en el rango
  range.load("values");
  await context.sync(); // Sincroniza el contexto para asegurarse de que los valores se carguen

  // Inicializa lastRow a 0 (en caso de que no se encuentren datos)
  let lastRow = 0;

  // Recorre los valores del rango en orden inverso para encontrar la última celda no vacía
  for (let i = range.values.length - 1; i >= 0; i--) {
    if (range.values[i][0] !== "") {
      lastRow = i + 11; // Ajusta el índice de la fila sumando 10 (ya que el rango comienza en la fila 10)
      break;
    }
  }

  // Retorna la última fila encontrada
  return lastRow;
}

export async function copyData() {
  
  await Excel.run(async (context) => {
    const ws = context.workbook.worksheets.getActiveWorksheet();
    
    const lastRow = await findLastRow(ws, context);

    if (lastRow > 0) {
      await copyDataPKIF(ws, lastRow, context);
      await copyDataVI(ws, lastRow, context);
      await copyDataV(ws, lastRow, context);
      await copyDataR(ws, lastRow, context);
      console.log("Datos copiados correctamente.");
    } else {
      console.log("No hay datos en el rango especificado.");
    }

  });

}

async function copyDataPKIF(ws, lastRow, context) {
  const range = ws.getRange('AA10:AB' + lastRow);
  range.load('values');
  await context.sync();

  const tableArray = range.values;
  let destRow = 10; // B10

  // Recorre el array y pega los valores en el nuevo rango
  for (let i = 0; i < tableArray.length; i++) {
    ws.getRange('B' + destRow).values = [[tableArray[i][0]]];
    ws.getRange('B' + (destRow + 1)).values = [[tableArray[i][1]]];
    
    // Incrementa la fila de destino en 2
    destRow += 2;
  }

  await context.sync();
}

