async function copyDataR(ws, lastRow, context) {
  const range = ws.getRange('AE10:AX' + lastRow);
  range.load('values');
  await context.sync();

  const tableArray = range.values;
  let destRow = 10; // Comienza en la fila 10

  for (let i = 0; i < tableArray.length; i++) {
    for (let j = 0; j < tableArray[i].length; j++) {
      const value = tableArray[i][j] !== "" ? tableArray[i][j] : 0;

      // Convertir el índice de columna a una letra (E = 4, F = 5, ..., X = 23)
      const columnLetter = String.fromCharCode('E'.charCodeAt(0) + j);

      ws.getRange(columnLetter + destRow).values = [[value]];
      ws.getRange(columnLetter + (destRow + 1)).values = [[value]];
    }
    // Incrementa la fila de destino en 2
    destRow += 2;
  }
  document.getElementById("macro").innerText = `¡Archivo listo para descargar!`;

  await context.sync();
}


export async function downFile() {
  
  await Excel.run(async (context) => {
    const ws = context.workbook.worksheets.getActiveWorksheet();
    
    const lastRowData = await findLastRowData(ws, context);

    if (lastRowData > 0) {
      const data = await readData(ws, lastRowData, context);
      console.log(data);
      await createFile(data, context);
      console.log("Datos copiados correctamente.");
    } else {
      console.log("No hay datos en el rango especificado.");
    }

  });

}

async function findLastRowData(ws, context) {
  const usedRange = ws.getUsedRange(true);
  usedRange.load('rowCount');
  await context.sync(); 

  const range = ws.getRange("B10:X" + usedRange.rowCount);

  // Carga los valores en el rango
  range.load("values");
  await context.sync(); // Sincroniza el contexto para asegurarse de que los valores se carguen

  // Inicializa lastRow a 0 (en caso de que no se encuentren datos)
  let lastRowData = 0;

  // Recorre los valores del rango en orden inverso para encontrar la última celda no vacía
  for (let i = range.values.length - 1; i >= 0; i--) {
    if (range.values[i][0] !== "") {
      lastRowData = i + 11; // Ajusta el índice de la fila sumando 10 (ya que el rango comienza en la fila 10)
      break;
    }
  }

  // Retorna la última fila encontrada
  return lastRowData;
}

async function readData(ws, lastRowData, context) {

  const range = ws.getRange('B10:X' + lastRowData);
  range.load('values');
  await context.sync();

  const tableArray = range.values;

  return tableArray; 
}

