async function getRowCount(ws, context) {
  const usedRange = ws.getUsedRange(true);
  usedRange.load("rowCount");
  await context.sync();

  const rowCount = usedRange.rowCount - 8; // Resta 9 para excluir las filas antes de la fila 10
  return rowCount;
}


async function createFile(data, context) {
  // Verifica que haya datos disponibles
  if (!data) {
    console.log("Datos no disponibles para crear el archivo.");
    return;
  }

  try {
    const activeSheet = context.workbook.worksheets.getActiveWorksheet();
    const rowCount = await getRowCount(activeSheet, context);

    // Construye el encabezado del archivo SDI
    const header = `# SONDEOS INDEPENDIENTES
# ------------------------
# version derecha izquierda Zabsoluta NROCAS
# ------- -------------------------
     2304    ${rowCount}      0      0     20
#  PK          vegetal  v+Inadec   Roca      Roca2     Roca3     Roca4     Roca5     Roca6     Roca7     Roca8     Roca9     Roca10    Roca11    Roca12    Roca13    Roca14    Roca15    Roca16    Roca17    Roca18    Roca19    Roca20
# ----------- --------- --------- --------- --------- --------- --------- --------- --------- --------- --------- --------- --------- --------- --------- --------- --------- --------- --------- --------- --------- --------- ---------\n`;

    // Formatea cada fila de datos según los requisitos
    const formattedData = data.map(row => {
      const formattedRow = row.map((value, index) => {
        // Aplica el formato .000 a la primera columna y .0000 a las demás
        if (index === 0) {
          return value.toFixed(3); // Formato .000 para la primera columna
        } else {
          return value.toFixed(4); // Formato .0000 para las demás columnas
        }
      });
      return formattedRow.join('   ');
    });

    // Crea el contenido del archivo SDI, concatenando el encabezado con los datos formateados
    const sdiContent = header + formattedData.join('\n');
    const blob = new Blob([sdiContent], { type: 'text/plain' });

    // Crea un enlace de descarga para el archivo
    const downloadLink = document.createElement("a");
    const url = URL.createObjectURL(blob);
    downloadLink.href = url;
    downloadLink.download = `archivo.sdi`;
    downloadLink.style.display = 'none';
    document.body.appendChild(downloadLink);

    document.getElementById("msg").innerText = `Archivo descargado correctamente`;
    
    // Simula el clic en el enlace para descargar el archivo
    downloadLink.click();

    // Limpia y remueve el enlace de descarga creado
    setTimeout(() => {
      document.body.removeChild(downloadLink);
      window.URL.revokeObjectURL(url);
    }, 0);

    console.log("Archivo creado correctamente.");
  } catch (error) {
    console.error("Error al crear el archivo:", error);
  }
}